import styled from 'styled-components';

export const Avatar = styled.img`
  width: 80px;
  border-radius: 200px;
`;

export const Title = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  display: table;
  
  &> * {
    font-size: 24px;
    font-weight: 600;
  }
`;